<template>
   <div>
       <spinal-list-item
               v-for="(item, index) in items"
               :parent="{parent, value}"
               :value="item.value"
               :item="item"
               @selected="onItemSelected"
       />
   </div>
</template>

<script>
  import SpinalListItem from './SpinalListItem.vue'
  export default {
    name: "SpinalList",
    components: {SpinalListItem},
    props: {
      items: {
        type: Array,
        default: function () {
          return [{
            parent: {parent: null, value: null},
            item: {},
            children: [],
            value: '',
          }];
        }
      },
      value:{
        type: Object
      }
    },
    methods: {
      onItemSelected: function (event) {
        this.$emit('item-selected',event)
      }
    }
  }
</script>

